/**
 * --------------------------------------------
 * XqAdminPage module-menu.ts
 * --------------------------------------------
 */

import {
  domReady
} from './xq-util'
/**
 * 后台管理左边的菜单
 */
// const SIDEBAR_ID = 'xqkeji-sidebar'
/**
 * 后台管理顶部模块菜单
 */
const TOP_MODULE_MENU_id = '#xqkeji-top-nav'

class XqModuleMenu {
  menuSwitch() {
    const container = document.querySelector(TOP_MODULE_MENU_id)
    if (container !== null) {
      const menus = container.querySelectorAll('li a:not([xq-widget])')
      // console.log(menus.length)
      // 循环添加处理事件
      for (const menu of menus) {
        menu.addEventListener('click', event => {
          event.preventDefault()
          const el = event.currentTarget as Element
          // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
          const target_id = el.getAttribute('href')!
          const target_el = document.querySelector(target_id)

          if (target_el?.classList.contains('d-none')) {
            for (const menu of menus) {
              if (menu !== el) {
                if (!menu.classList.contains('bg-dark')) {
                  menu.classList.add('bg-dark')
                }

                if (menu.classList.contains('bg-primary')) {
                  menu.classList.remove('bg-primary')
                }

                // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
                const menu_target_id = menu.getAttribute('href')!
                const target = document.querySelector(menu_target_id)
                if (!target?.classList.contains('d-none')) {
                  target?.classList.add('d-none')
                }
              } else {
                if (menu.classList.contains('bg-dark')) {
                  menu.classList.remove('bg-dark')
                }

                if (!menu.classList.contains('bg-primary')) {
                  menu.classList.add('bg-primary')
                }

                // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
                const menu_target_id = menu.getAttribute('href')!
                const target = document.querySelector(menu_target_id)

                if (target?.classList.contains('d-none')) {
                  target?.classList.remove('d-none')
                }
              }
            }
          }
        })
      }
      // 结束添加事件循环
    }
  }

  /** 初始化 */
  init() {
    this.menuSwitch()
  }
}

domReady(() => {
  const module_menu = new XqModuleMenu()
  module_menu.init()
})

export default XqModuleMenu
