/**
 * --------------------------------------------
 * XqAdminPage sidebar-overlay.ts
 * --------------------------------------------
 */

import {
  domReady
} from './xq-util'

const SIDEBAR_ID = '#xqkeji-sidebar'

const Config = {
  scrollbarTheme: 'os-theme-light',
  scrollbarAutoHide: 'leave'
}

class XqSidebarOverlay {
  _instance = null
  init(): void {
    // @ts-expect-error
    if (typeof OverlayScrollbars !== 'undefined') {
      const instance = this._instance
      if (!instance) {
        // @ts-expect-error
        this._instance = OverlayScrollbars(document.querySelector(SIDEBAR_ID), { // eslint-disable-line new-cap
          className: Config.scrollbarTheme,
          sizeAutoCapable: true,
          scrollbars: {
            autoHide: Config.scrollbarAutoHide,
            clickScrolling: true
          }
        })
      }
    }
  }

  destroy(): void {
    const instance = this._instance
    if (instance) {
      // @ts-expect-error
      instance.destroy()// eslint-disable-line @typescript-eslint/no-unsafe-call
      this._instance = null
    }
  }
}
const overlay_scroll = new XqSidebarOverlay()
domReady(() => {
  overlay_scroll.init()
})

export {XqSidebarOverlay, overlay_scroll}
